exports.push([module.id, "\n#edui429_iframe  .wrapper{\n    font-size: 12px!important;\n}\n.preview{\n    min-height: 200px;\n    width: 375px;\n}\n.editorBox{\n    margin: 10px 40px;\n}\n/*img{*/\n/*width: 100%;*/\n/*height: auto*/\n/*}*/\n", "", {"version":3,"sources":["C:/han work/fxj work/ifxj-lanmao-admin/src/views/src/views/vueEditor.vue"],"names":[],"mappings":";AACA;IACA,0BAAA;CACA;AACA;IACA,kBAAA;IACA,aAAA;CAEA;AACA;IACA,kBAAA;CACA;AACA,QAAA;AACA,gBAAA;AACA,gBAAA;AACA,KAAA","file":"vueEditor.vue","sourcesContent":["<style>\r\n    #edui429_iframe  .wrapper{\r\n        font-size: 12px!important;\r\n    }\r\n    .preview{\r\n        min-height: 200px;\r\n        width: 375px;\r\n\r\n    }\r\n    .editorBox{\r\n        margin: 10px 40px;\r\n    }\r\n    /*img{*/\r\n    /*width: 100%;*/\r\n    /*height: auto*/\r\n    /*}*/\r\n</style>\r\n<template>\r\n    <div class=\"editorBox\">\r\n        <div class=\"preview\" v-html=\"msg\"></div>\r\n        <vue-ueditor-wrap v-model=\"msg\" :config=\"myConfig\" @beforeInit=\"addCustomButtom\" :key=\"1\"></vue-ueditor-wrap>\r\n        <div @click=\"save()\">保存</div>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\n    import '../../static/UEditor/ueditor.config.js'\r\n    import '../../static/UEditor/ueditor.all.js'\r\n    import '../../static/UEditor/lang/zh-cn/zh-cn.js'\r\n    // import VueUeditorWrap from './components/vue-ueditor-wrap.vue';\r\n    // 检测构建之后vue-ueditor-wrap.js的可用性：\r\n    // import VueUeditorWrap from '../lib/vue-ueditor-wrap.min.js'; // ES6 Module\r\n    // const VueUeditorWrap = require('../lib/vue-ueditor-wrap.min.js'); // CommonJS\r\n    // const VueUeditorWrap = window.VueUeditorWrap; // 全局变量，通过 script 标签引入\r\n    export default {\r\n        components: {\r\n            // VueUeditorWrap\r\n        },\r\n        data () {\r\n            return {\r\n                msgData: '',\r\n                msg: '',\r\n                myConfig: {\r\n                    autoHeightEnabled: false,\r\n                    initialFrameHeight: 200,\r\n                    initialFrameWidth: '50%',\r\n                    UEDITOR_HOME_URL: 'static/UEditor/',\r\n                }\r\n            };\r\n        },\r\n        created(){\r\n\r\n            var imgs = document.getElementsByTagName('img');\r\n\r\n            for(var i in imgs)\r\n            {\r\n                var img = imgs[i];\r\n                img.style.width='100%';\r\n                img.style.height='auto';\r\n            }\r\n\r\n        },\r\n        watch:{\r\n            'msg':function(){\r\n                console.log(this.msg)\r\n            }\r\n        },\r\n        methods: {\r\n            save(){\r\n                this.msgData =   this.c\r\n            },\r\n            // 添加自定义按钮\r\n            addCustomButtom (editorId) {\r\n                window.UE.registerUI('test-button', function (editor, uiName) {\r\n                    // 注册按钮执行时的 command 命令，使用命令默认就会带有回退操作\r\n                    editor.registerCommand(uiName, {\r\n                        execCommand: function () {\r\n                            editor.execCommand('inserthtml', `<span>这是一段由自定义按钮添加的文字</span>`);\r\n                        }\r\n                    });\r\n                    // 创建一个 button\r\n                    var btn = new window.UE.ui.Button({\r\n                        // 按钮的名字\r\n                        name: uiName,\r\n                        // 提示\r\n                        title: '鼠标悬停时的提示文字',\r\n                        // 需要添加的额外样式，可指定 icon 图标，图标路径参考常见问题 2\r\n                        cssRules: \"background-image: url('/test-button.png') !important;background-size: cover;\",\r\n                        // 点击时执行的命令\r\n                        onclick: function () {\r\n                            // 这里可以不用执行命令，做你自己的操作也可\r\n                            editor.execCommand(uiName);\r\n                        }\r\n                    });\r\n                    // 当点到编辑内容上时，按钮要做的状态反射\r\n                    editor.addListener('selectionchange', function () {\r\n                        var state = editor.queryCommandState(uiName);\r\n                        if (state === -1) {\r\n                            btn.setDisabled(true);\r\n                            btn.setChecked(false);\r\n                        } else {\r\n                            btn.setDisabled(false);\r\n                            btn.setChecked(state);\r\n                        }\r\n                    });\r\n                    // 因为你是添加 button，所以需要返回这个 button\r\n                    return btn;\r\n                }, 0 /* 指定添加到工具栏上的哪个位置，默认时追加到最后 */, editorId /* 指定这个 UI 是哪个编辑器实例上的，默认是页面上所有的编辑器都会添加这个按钮 */);\r\n            },\r\n            // 添加自定义弹窗\r\n            addCustomDialog (editorId) {\r\n                window.UE.registerUI('test-dialog', function (editor, uiName) {\r\n                    // 创建 dialog\r\n                    var dialog = new window.UE.ui.Dialog({\r\n                        // 指定弹出层中页面的路径，这里只能支持页面，路径参考常见问题 2\r\n                        iframeUrl: '/customizeDialogPage.html',\r\n                        // 需要指定当前的编辑器实例\r\n                        editor: editor,\r\n                        // 指定 dialog 的名字\r\n                        name: uiName,\r\n                        // dialog 的标题\r\n                        title: '这是一个自定义的 Dialog 浮层',\r\n                        // 指定 dialog 的外围样式\r\n                        cssRules: 'width:600px;height:300px;',\r\n                        // 如果给出了 buttons 就代表 dialog 有确定和取消\r\n                        buttons: [\r\n                            {\r\n                                className: 'edui-okbutton',\r\n                                label: '确定',\r\n                                onclick: function () {\r\n                                    dialog.close(true);\r\n                                }\r\n                            },\r\n                            {\r\n                                className: 'edui-cancelbutton',\r\n                                label: '取消',\r\n                                onclick: function () {\r\n                                    dialog.close(false);\r\n                                }\r\n                            }\r\n                        ]\r\n                    });\r\n                    // 参考上面的自定义按钮\r\n                    var btn = new window.UE.ui.Button({\r\n                        name: 'dialog-button',\r\n                        title: '鼠标悬停时的提示文字',\r\n                        cssRules: `background-image: url('/test-dialog.png') !important;background-size: cover;`,\r\n                        onclick: function () {\r\n                            // 渲染dialog\r\n                            dialog.render();\r\n                            dialog.open();\r\n                        }\r\n                    });\r\n                    return btn;\r\n                }, 0 /* 指定添加到工具栏上的那个位置，默认时追加到最后 */, editorId /* 指定这个UI是哪个编辑器实例上的，默认是页面上所有的编辑器都会添加这个按钮 */);\r\n            }\r\n        }\r\n    };\r\n</script>\r\n"],"sourceRoot":""}]);

